.search {
  color: #d9ab73;
  position: sticky;
  text-align: center;
  margin-bottom: 5vh;
  z-index: 20;
  top: 0vh;
  padding-top: 1px;
  padding-bottom: 5vh;
}

/* .search {
  color: #d9ab73;
  text-align: center;
  margin-bottom: 5vh;
} */



@media (max-width: 991px) {

  .storeFinderInfo  {
    height: calc(100vh - 70px);
    overflow-y: scroll;
  }

  .stickyMap {
    position: relative !important;
  }
  .mapCol {
    position: relative;
    right:0;
  }
  .search {
    display: none;
  }
  .storeCards {
    margin-top: 20px;
    /* z-index: 1000; */
  }
  .mobileSearch {
    display: block !important;
    z-index: 5;
  }
  .desktopMap {
    display: none;
  }
  .mobileMap {
    display: block !important;
  }

  
}



.mobileMap {
  display: none;
}
.mobileSearch {
  color: #d9ab73;
  text-align: center;
  margin-bottom: 5vh;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  display: none;
}
.search h1 {
  margin-bottom: 1vh;
}

.mapCol {
  padding: 0px !important;
}

.selectedStoreInfo {
  padding: 0px !important;
}

.stickyMap {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding: 0px !important;
}

